#app {
    /* full screen clickaway detection*/
    min-height: 100vh;
}

.event-item-0:hover {
    background: #bd1c1c !important;
}

.rct-item-handler {
    cursor: ew-resize !important;
}

.event-item-1:hover {
    background: #2d8f15 !important;
}

.event-item-2:hover {
    background: #ededed !important;
}

#modal-actions button {
    margin: 10px;
}

#modal-actions #confirm-reset-button {
    background-color: #6F1111;
}

#modal-actions #confirm-reset-button:hover {
    background-color: #bd1c1c;
}


.react-time-picker input {
    color: white
}

.slider {
    height: 25px;
}

.slider-thumb {
    cursor: pointer;
    background: #003478;
    border: 5px solid #003478;
    border-radius: 10%;
    display: block;
    outline: none;
}

.slider-track {
    position: relative;
    background: #ddd;
    top: 15px;
    height: 4px;
}

.slider-track.slider-track-0 {
    background: #83a9ff;
}

.context-menu {
    width: 200px;
    min-height: 100px;
    background-color: #1C1C1C;
    box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, .2);
    border-radius: 10%;
    padding: 1rem;
}

#add-day-button {
    text-align: center;
}

#add-day-button button {
    border-radius: 50%;
    font-size: 2rem;
    width: 75px;
    height: 75px;
    justify-content: center;
}

#open-modal-button {
    border-radius: 50%;
    font-size: 1.5rem;
    width: 50px;
    height: 50px;
    justify-content: center;
    position: fixed;
    right: 20px;
    bottom: 20px;
}


.timeline-container {
    display: flex;
    margin-bottom: 20px;
}

.day-side-details {
    width: 100px;
    background-color: #0D0D0D;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.day-side-details .arrow-button {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    justify-content: center;
    background-color: #2c2c2c;
}

.day-side-details .arrow-button:hover {
    background-color: #434343;
}


.day-side-details .day-number-x-button {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    justify-content: center;
}

.day-side-details .day-number-x-button:hover .button-day-number {
    display: none;
}

.day-side-details .day-number-x-button:hover .button-x {
    display: flex;
    justify-content: center;
}

.day-side-details .button-x {
    display: none;
}

.react-calendar-timeline * {
    box-sizing: border-box;
}

.react-calendar-timeline .rct-outer {
    display: block;
    overflow: hidden;
    border-radius: 11px;
    filter: drop-shadow(-6px 4px 5px rgba(0, 0, 0, .25));
    white-space: nowrap;
}

.react-calendar-timeline .rct-scroll {
    display: inline-block;
    white-space: normal;
    vertical-align: top;
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-touch-action: none;
    touch-action: none;
    background-color: #121212;
}

.react-calendar-timeline .rct-item:hover {
    z-index: 88;
}

.react-calendar-timeline .rct-item .rct-item-content {
    position: sticky;
    position: -webkit-sticky;
    left: 0px;
    overflow: hidden;
    display: inline-block;
    border-radius: 2px;
    padding: 0 6px;
    height: 100%;
}

.react-calendar-timeline .rct-sidebar {
    overflow: hidden;
    white-space: normal;
    background-color: #0D0D0D;
    display: inline-block;
    vertical-align: top;
    text-align: right;
    position: relative;
    box-sizing: border-box;
    border-right: 0px solid #bbb;
}

.react-calendar-timeline .rct-sidebar.rct-sidebar-right {
    border-right: 0;
    border-left: 1px solid #bbb;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row {
    padding: 0 4px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    box-sizing: border-box;
    margin: 0;
    border-bottom: 1px solid #bbb;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-odd {
    /*background: rgba(0, 0, 0, 0.05);*/
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-even {
    /*background: transparent;*/
}

.react-calendar-timeline .rct-vertical-lines .rct-vl {
    position: absolute;
    border-left: 1px solid #626262;
    z-index: 30;
}

.react-calendar-timeline .rct-vertical-lines .rct-vl.rct-vl-first {
    /*border-left-width: 2px;*/
}

.react-calendar-timeline .rct-horizontal-lines {
    -webkit-user-select: none;
    -moz-user-select: -moz-none;
    -ms-user-select: none;
    user-select: none;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even,
.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
    border-bottom: 1px solid #272727;
    box-sizing: border-box;
    z-index: 40;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
    /*background: rgba(0, 0, 0, 0.05);*/
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even {
    background: transparent;
}

.react-calendar-timeline .rct-cursor-line {
    position: absolute;
    width: 2px;
    background: #2196f3;
    z-index: 51;
}

.react-calendar-timeline .rct-dateHeader {
    display: flex;
    align-items: top;
    justify-content: left;
    height: 100%;
    border-bottom: 0px solid #bbb;
    font-size: 14px;
    border-left: 1px solid #626262;
    padding: 0px 3px;
    border-top: 0px;
}

.react-calendar-timeline .rct-dateHeader-primary {
    background-color: initial;
    border-left: 1px solid #bbb;
    border-right: 1px solid #bbb;
    color: #fff;
}

.react-calendar-timeline .rct-header-root {
    border-bottom: 0px solid #bbb;
}

.react-calendar-timeline .rct-calendar-header {
    border: 0px solid #bbb;
}

.react-calendar-timeline {
    width: 100%;
    overflow: hidden;
}